.appbar-button {
  color: black;
}

.main-container {
  text-align: center;
  background-image: url("../public/background.png");
  background-size: cover;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-container {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
    background-color: #f6f6fa;
}

h1 {
  font-size: 52px;
  padding: 0;
  margin: 0;
}

h2 {
  font-size: 28px;
  padding: 0;
  margin: 16px;
}

h3 {
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
  font-size: 18px;
}

.intro-container {
  margin-top: 30vh;
  display: flex;
  align-self: flex-start;
  flex-direction: column;
}

.intro-text {
  color: white;
}

.intro-quote {
  font-weight: 300;

}

.about-content-container {
  display: flex;
  margin: 48 0px;
  justify-content: center;
}

.about-details-container {
  width: 50vw;
}

.about-header-container {
  padding: 96px 0;
}

.about-portrait-container {
  width: 50vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.about-portrait {
  border-radius: 50%;
}

.about-text {
  padding: 8px 32px 8px;
  line-height: 28px;
}

.about-quote {
  font-size: 32px;
  padding-bottom: 32px;
  font-weight: 300;
}

.about-skill-text {
  opacity: 0.5;
  font-weight: 600;
}

.projects-container {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: #f6f6fa;
}

.project-cards-container {
  margin-top: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.project-card-text {
  margin-top: 8px;
  text-align: left;
}

.project-skills-container {
  margin-bottom: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.project-skill {
  margin: 4px;
  padding: 4px 8px;
  border-radius: 6px;
  background-color: #edeef6;
  white-space: nowrap;
}

.project-skill-text {
  font-size: 16px;
  opacity: 0.6;
  font-weight: 500;
}

.contact-gradient {
  height: 200px;
  border-top: 60px solid transparent;
  border-left: 100vw solid #1d1c1d;
}

.contact-container {
  margin-top: -240px;
  height: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-header-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 300px;
  margin-top: 48px;
}

.contact-text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom-bar-container {
  margin-top: 240px;
  height: 300px;
  background-color: #1d1c1d;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.bottom-bar-header {
  color: #f9df9b;
}

.bottom-bar-text {
  font-size: 14px;
  color: #f9df9b;
}

.bottom-bar-socials-container {
  margin: 36px 0 16px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bottom-bar-social {
  margin: 0 4px;
}